var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"page-container"},[_c('el-breadcrumb',[_c('el-breadcrumb-item',{attrs:{"to":{ path: '/' }}},[_vm._v("首頁")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("服務項目")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("美麗重現")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("墊下巴")])],1),_vm._ssrNode(" "),_c('Title',{attrs:{"title":"墊下巴","titleEn":"CHIN IMPLANT"}}),_vm._ssrNode(" <div class=\"article-container service-page\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/surgerySculpture/chinImplant/banner.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/chinImplant/banner.jpg")))+" alt=\"墊下巴 Banner\"></picture></div> <div class=\"article-container\"><h2>下巴是全臉線條完美的關鍵</h2> <p>近幾年迅速爆紅的網路美女們，都有一個共同點「小V臉」。回歸於現實生活中照片和本人往往有落差。美照的必備原則，除了自拍基本角度SOP、美肌濾鏡，另一個關鍵是修飾臉型輪廓線。但，妳想永遠只用騙照見粉絲？</p> <p>當我們從原處看一個人，先看到的不是眼睛和鼻子，而是臉部的整體輪廓線，也因此雕塑下半臉輪廓線逐漸受到關注。</p> <p>目前的下巴整形可分為<b>「微整形填補下巴」</b>或<b>「墊下巴手術」</b>兩大項。</p></div> <div class=\"article-container\"><h2>微整形填補下巴</h2> <h3>玻尿酸、洢漣絲 填補下巴</h3> <p>類似於人體原有的細胞成分，植入式的玻尿酸、洢漣絲注射後可以被人體吸收分解，所以，安全性相對比較高，不容易會有排斥問題。因為玻尿酸、洢漣絲可以被吸收代謝， 相對於整形手術，整復的方法與成本都相對簡單，因此，很多人會選擇先施打玻尿酸、洢漣絲，看看下巴延長的效果是否符合需求後，再進一步接受整形的手術。</p></div> <div class=\"article-container\"><h2>墊下巴手術</h2> <h3>卡麥拉</h3> <p>一般俗稱為「卡麥拉」Chimera是一種“複合材質”的人工假體，內層矽膠、外層Goretex(ePTFE)，合為一體成型。</p> <p>卡麥拉植入物就是包覆一層Gore-Tex的人工假體，與人體的相容性高，植入後觸感較自然且不易位移，手術一般在下唇的口內作切口，並將假體放置在下頜骨前方，達到拉長下巴或雕塑下巴型狀等效果。</p></div> <div class=\"article-container\"><h3>什麼是下巴黃金比例</h3> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/surgerySculpture/chinImplant/1.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/chinImplant/1.jpg")))+" alt=\"下巴黃金比\"></picture> <p>依人體美學的黃金比例，下巴位於臉部下方中心，其形狀左右了臉部兩側的平衡。下巴的長度應佔全臉長度的1/6，而下巴最尖的位置，應該與眉心在同一個垂直線上。</p> <ul><li>+ 嘴唇下方陰影不可以太深</li> <li>+ 臉部兩側線條呈現柔順曲線</li> <li>+ 鼻頭連接嘴唇中央至下巴尖端呈一直線，對稱無偏移</li> <li>+ 下臉部占全臉1/3</li> <li>+ 側面鼻頭、嘴唇、下巴尖端呈現一直線(E-Line)</li> <li>+ 下唇-下巴-頸部形成柔順曲線。不可後縮，往上、往下旋轉幅度適中</li> <li>+ 嘴唇下方凹陷處至兩唇間的距離為凹陷處至下巴尖端距離的1/2</li> <li>+ 下巴的角度從側面看最好呈45度角</li></ul></div> <div class=\"article-container\"><h2>美麗案例</h2> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/surgerySculpture/chinImplant/案例1.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/chinImplant/案例1.png")))+" alt=\"案例1\"></picture> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/surgerySculpture/chinImplant/案例2.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/surgerySculpture/chinImplant/案例2.png")))+" alt=\"案例2\"></picture> <h4>療程效果會因個人體質而有所差異</h4></div> <div class=\"article-container\"><h2>術前注意事項</h2> <ul><li>+ 術前術後請盡量避免吸煙，以免造成血管收縮，影響傷口癒合。</li> <li>+ 術前術後7天，請暫停服用阿斯匹靈/其他類似藥物、銀杏類/增加血液循環、維他命E之保健食品，避免增加出血問題。</li> <li>+ 多吃蔬果，增加纖維素與維他命C的攝取，增加傷口癒合力。</li> <li>+ 手術前一天晚上，若醫師有開立處方藥物，請記得服用。</li> <li>+ 手術當天請勿戴金屬物品、活動假牙，並穿寬鬆衣物。</li></ul></div> <div class=\"article-container\"><h2>術後小叮嚀</h2> <ul><li>+ 手術後下顎需固定帶固定3天，以減輕腫脹和不適。</li> <li>+ 內縫線在術後10天左右拆除。也有些口內縫線將會自行脫落。切口一般5天左右拆線。</li> <li>+ 術後幾日內，手術區腫脹，微笑和談話會感到有些不適。</li> <li>+ 傷口護理：用餐後務必做好口腔清潔，使用不含酒精漱口水漱口，若有殘渣卡於縫線上，則用使用空針抽取食用水沖洗，並使用乾淨棉棒輕柔的移除，以輕壓的方式清潔傷口。</li> <li>+ 術後2-4天腫脹較明顯,就寢時可以墊高頭部,以減緩腫脹不適。7-10天會逐漸消腫,要完全恢復約3-6個月(以上依個人體質天數會縮短或增減)。</li> <li>+ 術後當天傷口縫線處若有微滲血是正常現象,可用無菌棉枝或紗布輕壓。</li></ul> <h4>本療程的效果可能因個人體質的不同而有差異，請務必與醫師溝通諮詢</h4></div>")],2)}
